.card__container {
  margin: 20px;
  border-radius: 15px !important;
  -webkit-box-shadow: 0px 1px 27px -3px rgba(255, 255, 255, 1);
  -moz-box-shadow: 0px 1px 27px -3px rgba(255, 255, 255, 1);
  box-shadow: 0px 1px 27px -3px rgba(255, 255, 255, 1) !important;
}

.card__media {
  height: 270px;
  width: 400px;
  transition: all 0.2s ease-in-out;
}

.card__backContent {
  height: 320px;
  width: 400px;
  transition: all 0.2s ease-in-out;
}

.card__list {
  padding: 10px !important;
}

.card__frontContent {
  width: 400px;
  padding: 10px !important;
  font-weight: 520 !important;
  color: #010606 !important;
}

.card__media > img {
  width: 400px;
  height: 300px;
}

.card__media > p {
  padding: 10px;
}

.card__modal {
  display: flex;
  align-items: center;
  justify-content: center;
}

@media screen and (max-width: 1350px) {
  .scrollable {
    overflow-y: scroll;
  }

  .scrollable::-webkit-scrollbar {
    display: none;
  }

  .card__frontContent {
    width: 350px;
  }

  .card__backContent {
    height: 300px;
    width: 350px;
  }

  .card__media {
    height: 250px;
    width: 350px;
  }
}

@media screen and (max-width: 1075px) {
  .card__frontContent {
    width: 300px;
  }

  .card__backContent {
    height: 250px;
    width: 300px;
  }

  .card__media {
    height: 200px;
    width: 300px;
  }

  .card__video {
    width: 426px !important;
    height: 240px !important;
  }
}
